.intro {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  padding: 15.5rem 0 10rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 155px 0 100px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    padding: 6rem 0 5.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 16vh 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 16vh 0 5vh;
  }
}

.intro__title {
  margin: 0;
  max-width: 99rem;
  text-transform: uppercase;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 12rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  @media (min-width: $stop-scaling) {
    max-width: 990px;
    font-size: 120px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    max-width: 100%;
    font-size: 3.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 10rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 5rem;
  }

  .word-text + .word-text {
    position: relative;
    top: -0.21em;
  }

  .word-text:nth-child(1) span {
    &:nth-child(1) { // Т
      transition-delay: 115ms;
    }

    &:nth-child(2) { // А
      transition-delay: 50ms;
    }

    &:nth-child(3) { // И
      transition-delay: 0ms;
    }

    &:nth-child(4) { // Н
      transition-delay: 50ms;
    }

    &:nth-child(5) { // С
      transition-delay: 85ms;
    }

    &:nth-child(6) { // Т
      transition-delay: 50ms;
    }

    &:nth-child(7) { // В
      transition-delay: 0ms;
    }

    &:nth-child(8) { // Е
      transition-delay: 155ms;
    }

    &:nth-child(9) { // Н
      transition-delay: 85ms;
    }

    &:nth-child(10) { // Н
      transition-delay: 0ms;
    }

    &:nth-child(11) { // Ы
      transition-delay: 85ms;
    }

    &:nth-child(12) { // Й
      transition-delay: 55ms;
    }
  }

  .word-text:nth-child(2) span {
    &:nth-child(1) { // О
      transition-delay: 320ms;
    }

    &:nth-child(2) { // Т
      transition-delay: 360ms;
    }

    &:nth-child(3) { // П
      transition-delay: 290ms;
    }

    &:nth-child(4) { // У
      transition-delay: 185ms;
    }

    &:nth-child(5) { // С
      transition-delay: 320ms;
    }

    &:nth-child(6) { // К
      transition-delay: 230ms;
    }
  }
}

.intro__content {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 6.3rem;

  @media (min-width: $stop-scaling) {
    padding-left: 63px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    justify-content: flex-start;
    padding-left: 2.5rem;
  }
}

.intro__info {
  flex: none;
  width: 50rem;
  max-width: 50%;

  @media (min-width: $stop-scaling) {
    width: 500px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }
}

.intro__message {
  flex: none;
  width: 50.9rem;
  max-width: 50%;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.27;
  opacity: 0;
  transform: translateY(20%);
  transition-property: transform, opacity;
  transition-duration: 0.5s;
  transition-delay: 0.2s;

  @media (min-width: $stop-scaling) {
    width: 509px;
    font-size: 24px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    width: 100%;
    max-width: 100%;
    font-size: 1.6rem;
    line-height: 1.2;
  }

  .active & {
    opacity: 1;
    transform: translateY(0%);
  }

  p {
    margin: 0;
  }

  svg {
    @media (min-width: $stop-scaling) {
      width: 16px;
      height: 16px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      width: 1.1rem;
      height: 1.1rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 1.1rem;
      height: 1.1rem;
    }
  }
}

.intro__label {
  margin: 0 0 1.6rem;
  max-width: 25rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.5s;
  transition-delay: 0.8s;

  @media (min-width: $stop-scaling) {
    margin: 0 0 16px;
    width: 250px;
    font-size: 24px;
  }

  .active & {
    opacity: 1;
  }
}

.intro__date {
  margin: 0 0 0 -0.5rem;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  @media (min-width: $stop-scaling) {
    margin: 0 0 0 -5px;
    font-size: 48px;
  }

  .word-text:nth-child(1) span {
    &:nth-child(1) { // О
      transition-delay: 0.7s + 75ms;
    }

    &:nth-child(2) { // 1
      transition-delay: 0.7s + 45ms;
    }
  }

  .word-text:nth-child(2) span {
    &:nth-child(1) { // -
      transition-delay: 0.7s;
    }
  }

  .word-text:nth-child(3) span {
    &:nth-child(1) { // 3
      transition-delay: 0.7s + 100ms;
    }

    &:nth-child(2) { // 1
      transition-delay: 0.7s + 30ms;
    }

    &:nth-child(3) { // .
      transition-delay: 0.7s + 35ms;
    }

    &:nth-child(4) { // 0
      transition-delay: 0.7s + 45ms;
    }

    &:nth-child(5) { // 5
      transition-delay: 0.7s + 130ms;
    }
  }

  .word-text:nth-child(4) span {
    &:nth-child(1) { // /
      transition-delay: 0.7s + 45ms;
    }
  }

  .word-text:nth-child(5) span {
    &:nth-child(1) { // 2
      transition-delay: 0.7s + 0ms;
    }

    &:nth-child(2) { // 0
      transition-delay: 0.7s + 100ms;
    }

    &:nth-child(3) { // 2
      transition-delay: 0.7s + 75ms;
    }

    &:nth-child(4) { // 0
      transition-delay: 0.7s + 30ms;
    }
  }
}
